<template>
  <div id="app">
    <div class="bkg-filter">
      <div class="bkg-color"></div>
    </div>
    <div class="container">
      <div class="card">
        <h3
          class="name"
          :title="_get(curItemInfo,'snippet.title','')"
        >{{_get(curItemInfo,"snippet.title",'')}}</h3>

        <div style="display: none">
          <youtube ref="youtube" @playing="playing" :player-vars="playerVars"></youtube>
        </div>
        <!-- <youtube ref="youtube" @playing="playing" :player-vars="playerVars"></youtube> -->
        <!-- <button @click="getList">getList</button> -->

        <div class="actions">
          <font-awesome-icon icon="chevron-left" @click="prevVideo" />
          <font-awesome-icon icon="pause" v-if="isPlaying" @click="pauseVideo" />
          <font-awesome-icon icon="play" v-else @click="playVideo" />
          <font-awesome-icon icon="chevron-right" @click="nextVideo" />
        </div>
      </div>
      <div class="card">
        <div class="wrapper-list-music">
          <div
            v-for="(item,index) in items"
            :key="item.id"
            :class="['list-music__item',{ item_playing: playlistIndex === index }]"
          >
            {{index+1}}. {{
            item.snippet.title}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import _get from "lodash/get";

const API = axios.create({
  baseURL: "https://www.googleapis.com/youtube/v3/",
  timeout: 1000
});

export default {
  name: "App",
  data() {
    return {
      isPlaying: true,
      playerVars: {
        autoplay: 1,
        playlist: ""
      },
      params: {
        maxResults: 50,
        playlistId: "PLi0VfqdOHJYRV7omipap8B0gbdGOChDmE",
        part: "snippet, contentDetails",
        key: "AIzaSyBLRzr9tWL2oE0DS_HsvlV8vSNU1kTKxiw"
      },
      items: [],
      playlistIndex: 0,
      playerInfo: {}
    };
  },
  methods: {
    _get,
    playVideo() {
      this.player.playVideo();
      this.isPlaying = true;
    },
    pauseVideo() {
      this.player.pauseVideo();
      this.isPlaying = false;
    },
    prevVideo() {
      this.player.previousVideo();
      this.setPlaylistIndex(-1);
    },
    nextVideo() {
      this.player.nextVideo();
      this.setPlaylistIndex(1);
    },
    playing() {
      // console.log("o/ we are watching!!!");
    },
    getList() {
      this.player.getPlaylistIndex().then(data => console.log(44, data));
      this.player.getPlaylist().then(data => console.log(333, data));
    },
    setPlaylistIndex(index) {
      if (this.playlistIndex + index < 0) {
        this.playlistIndex = 0;
        return;
      }

      if (this.playlistIndex + index > this.items.length - 1) {
        this.playlistIndex = this.items.length - 1;
        return;
      }
      this.playlistIndex = this.playlistIndex + index;
    }
  },
  computed: {
    player() {
      return this.$refs.youtube.player;
    },
    curItemInfo() {
      return this.items[this.playlistIndex];
    }
  },
  watch: {
    playerVars: {
      playlist: {}
    }
  },
  mounted() {
    const self = this;
    API.get("playlistItems/", { params: this.params })
      .then(function({ data }) {
        // handle success
        if (data) {
          let playlist = [];
          data.items.forEach(item => {
            playlist.push(item.contentDetails.videoId);
          });
          self.items = data.items;
          self.playerVars.playlist = playlist.toString();
          self.player.loadPlaylist(playlist);
        }
      })
      .catch(function(error) {
        // handle error
        console.log(error);
      });
  }
};
</script>

